
.app-footer {
  position: absolute;
  bottom: 0;
/*bottom 0
  !*z-index 300*!*/
  width: 100%;
  display: -webkit-box;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-around;
          justify-content: space-around;
  background-color: #fbfbfb;
  border-top: 1px solid #dbdbdb;
  box-sizing: border-box;
  height: 50px;
  -webkit-box-align: center;
  -webkit-align-items: center;
          align-items: center;
  font-size: 12px;
  color: #b4b5bc;
}
.app-footer .footer-item {
  display: -webkit-box;
  display: -webkit-flex;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-box-align: center;
  -webkit-align-items: center;
          align-items: center;
}
.app-footer .footer-item .icon {
  font-size: 22px;
}
.app-footer .footer-item p {
  margin-top: 5px;
}
.app-footer .act {
  color: #ff4800;
}


